// ----------------------------------------------------------------------------
// Regular checkboxes
.form-checkboxes {
  .ie10 & {
    p {
      max-width: 90%;
    }
  }
  .form-checkbox {
    // Hide default checkboxes
    @extend %element-invisible;

    .desktop & {
      &:hover + label {
        &::before {
          background: rgba($black, .6);
          border-color: rgba($black, .6);
        }

        &::after {
          opacity: .8;
          transform: scale(1);
        }
      }

      &:disabled + label {
        cursor: not-allowed;
        color: $black;

        &::before {
          background: rgba($black, .2);
        }

        &::after {
          opacity: .8;
          transform: scale(0);
        }
      }

      &:disabled:checked + label {
        &::before {
          background: rgba($black, .7);
        }
        &::after {
          opacity: .8;
          transform: scale(1);
        }
      }
    }

    // Checked checkbox states
    &:checked + label {

      &::before {
        background: $black;
        border-color: $black;
      }

      &::after {
        opacity: 1;
        transform: scale(1);
      }
    }

    &:disabled + label {
      cursor: not-allowed;
      color: $black;

      &::before {
        background: rgba($black, .2);
      }

      &::after {
        opacity: .8;
        transform: scale(0);
      }
    }
  }

  // Default checkbox styling
  .form-type-checkbox {
    > label {
      position: relative;
      cursor: pointer;
      display: flex;
      align-items: flex-start;
      font-weight: $f_normal;
      line-height: 1.45;
      margin-bottom: 0;

      &::before {
        display: inline-block;
        vertical-align: text-bottom;
        content: ' ';
        background: $black;
        border: 1px solid $black;
        height: 20px;
        width: 20px;
        min-width: 20px;
        margin: 0 10px 0 0;
        transition: background .3s ease;
      }

      &::after {
        content: ' ';
        background: url(../img/check.svg) no-repeat center;
        position: absolute;
        left: 0;
        top: 0;
        opacity: 0;
        transition: opacity .3s ease, transform .2s ease;
        color: $white;
        transform: scale(.4);
        transform-origin: center;
      }
    }
  }
}

.request-form__agree {
  max-width: 560px;
  margin: 0 auto;
  font-size: 14px;

  @include media-q(min, 768) {
    font-size: 18px;
  }

  label {
    a {
      color: $white;
      text-decoration: underline;
      font-size: 14px;
      display: block;

      @include media-q(min, 768) {
        font-size: 18px;
        display: inline-block;
      }
    }
  }
}

.checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 20px;
}
.checkbox + label {
  position: relative;
  padding: 0 0 0 45px;
  cursor: pointer;
  display: block;
}
.checkbox + label:before {
  content: '';
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  width: 28px;
  height: 28px;
  transition: .2s;
  background-color: $c_dark;
  border-radius: 5px;
}

.checkbox:checked + label:after {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 4px;
  transition: .2s;
  color: #6BD2B9;
  font-size: 14px;

  @extend %font-icon;
  @extend %icon-check;
}